import React from 'react'
import Link from 'next/link'

const links = [
  { href: 'https://meetups.twitch.tv/los-angeles/', label: 'Twitch LA' },
  { href: 'https://www.meetup.com/ocstreamers', label: 'OC Streamers' },
  { href: 'https://meetups.twitch.tv/san-diego/', label: 'Twitch SD' }
];

const Card = (props) => {
  const {name} = props;
  let link = links.find(l => l.label === name);
  if (!link) link = { href:'https://meetups.twitch.tv/', lable: 'Twitch Meetups' };

  return (
    <>
      <Link href={link.href} label={link.label}>
        <a className='card'>
          <h3>{name}</h3>
          <h4>Next Event</h4>
          <p>TBD</p>
        </a>
      </Link>  
      <style jsx>{`
        .card {
          padding: 18px;
          width: 180px;
          height: 300px;
          color: #fff;
          text-align: left;
          text-decoration: none;
          background: #660000;
          transition: filter 150ms, transform 200ms;
          filter: drop-shadow(0 5px 3px rgba(0,0,0,0.4));
          border-radius: 20px;
        }
        .card:hover {
          transform: scale(1.06);
          filter: drop-shadow(0 8px 6px rgba(0,0,0,0.3));
        }
        .card h3 {
          margin: 0;
          color: #fff;
          font-size: 22px;
          font-weight: 700;
          position: absolute;
          bottom: 10px;
        }
        .card h4 {
          font-weight: 700;
          font-size: 20px;
          margin: 12px 0 0;
        }
        .card p {
          margin: 0;
          padding: 6px 0 0;
          font-size: 18px;
          color: #fff;
          font-weight: 400;
        }
      `}</style>
    </>
  )
};

export default Card
